body{
    margin: 0;
    padding: 0;
    background-color: rgb(15, 15, 15); 
    overflow-x: hidden;
    overflow-y: hidden;
    display: flex;
    justify-content: center;
}





html{
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body button{
    cursor: pointer;
}

